<template>
    <el-dialog
    v-bind:title="'Договор №' + general.contract_number"
    :visible="taskDetailsDialogVisible"
    v-on:close="closeDetails()"
    width="600">

        <el-collapse v-model="collapseTitles">
            <el-collapse-item title="Информация">
                <p>Тип договора: {{ general.contract_type }}</p>
                <p>ФИО: {{ particular.promiser.fullname }}</p>
                <p>Дней просрочки: {{ general.delay }}</p>
                <p>Общая задолженность: {{ general.debt }}руб.</p>
            </el-collapse-item>
            <el-collapse-item title="Контакты">
                <p 
                    v-for="(contact, index) in particular.promiser.contacts"
                    v-bind:contact="contact"
                    v-bind:key="index">
                    {{ contact.phone }} {{ contact.owner }}
                </p>
            </el-collapse-item>
            <el-collapse-item title="Комментарии">
                <el-card
                    v-for="(comment, index) in particular.comments"
                    v-bind:comment="comment"
                    v-bind:key="index" >
                        <h5>{{ comment.author }}:</h5>
                        <p>{{ comment.content }}</p>
                        <p>{{ comment.modified }}</p>
                </el-card>
            </el-collapse-item>
        </el-collapse>
        <!-- <el-col>
            <h4>Информация</h4>
            <p>Тип договора: {{ data.dataDetail.type }}</p>
            <p>ФИО: {{ data.dataDetail.fullName }}</p>
            <p>Дней просрочки: {{ data.dataDetail.delay }}</p>
            <p>Общая задолженность: {{ data.dataDetail.commonDebt }} руб.</p>
        </el-col>
        <el-col>
            <h4>Контакты</h4> -->
            <!-- <p v-for="contact in data.dataDetail.contacts">
                {{contact.tel}}
                {{contact.owner}}
                {{contact.lastCall}}
            </p> -->
        <!-- </el-col> -->

        <!-- Комментарии -->

        <el-button-group style="color:aqua">
            <el-button v-if="store.getLocateName(detailsTaskId) == 'commonQuery'" @click="store.takeTask(detailsTaskId)" type="warning" title="Вернуться к обработке позже (отложить договор себе)">Отложить себе</el-button>
            <el-button v-if="store.getLocateName(detailsTaskId) == 'myself'" @click="store.returnTask(detailsTaskId)" type="danger" title="Действий по обработке не было, ОТМЕНА (вернуть в общую очередь)">Вернуть в очередь</el-button>
            <el-button @click="store.finishTask(detailsTaskId)" type="success" title="Обработано. Не возвращаться к договору в ближайшее время">Завершить сделку</el-button>
        </el-button-group>


    <!-- <span>This is a task #{{this.detailsTaskId}}</span>
    <span slot="footer" class="dialog-footer">
        <el-button @click="closeDetails()">Cancel</el-button>
        <el-button type="primary" @click="closeDetails()">Confirm</el-button>
    </span> -->
    </el-dialog> 
</template>

<script>
    import store from '../store/custom';

  export default {
    data() {
        return {
            collapseTitles: [],
            store:  store,
        }
    },
    computed: {
        general() {
            return this.store.state.generalData.find(item => item.id === this.detailsTaskId);
        },
        particular() {
            return this.store.getDetailsTask(this.detailsTaskId);
        },
        locate() {
            let result= '';
            if (this.store.state.myselfIds.includes(this.detailsTaskId)) {
                result = "myself";
            }
            else if (this.store.state.commonIds.includes(this.detailsTaskId)) {
                result = "commonQuery";
            }
            else {
                result = "ПИЗДЕЦ((";
            }
            return result;
        }
    },
    props: ['taskDetailsDialogVisible', 'detailsTaskId'],
    methods: {
        closeDetails() {
            this.$emit('close-details-dialog')
        },
    },
  };
</script>