<template>
  <div id="app">
    <el-menu>
      <NavMenu></NavMenu>
    </el-menu>

    <el-container style="height:80vh">
      <el-container v-bind:class="{'list-deferred': isMyselfListNotEmpty}">
        <!-- Создаем список Отложенных заданий -->
        <TaskList 
              v-bind:list="store.state.myselfIds" 
              v-bind:is_common="false"
              >
        </TaskList>
      </el-container>
      <el-container>
        <!-- Создаем список Ожидающих заданий -->
        <TaskList 
              v-bind:list="store.state.commonIds" 
              v-bind:is_common="true"
              >
        </TaskList>
      </el-container>
    </el-container>
    <TaskDetailsDialog
        v-bind:taskDetailsDialogVisible="store.state.taskDetailsDialogVisible"
        v-bind:detailsTaskId="store.state.detailsTaskId"
        v-on:close-details-dialog="store.state.taskDetailsDialogVisible = false"
        >
    </TaskDetailsDialog>
  </div>
</template>

<script>
import NavMenu from './components/NavMenu.vue'
import TaskList from './components/TaskList.vue'
import TaskDetailsDialog from './components/TaskDetailsDialog.vue'

import dataList from './assets/js/data.js' 

import store from './store/custom';

const localKey = "debtors-app";

export default {
  name: 'app',
  components: {
    NavMenu,
    TaskList,
    TaskDetailsDialog,
  },
  computed: {
    isMyselfListNotEmpty() {
      return !this.store.state.myselfIds.length > 0;
    }
  },
  data() {
    return {
      commonIds: [],
      myselfIds: [],
      generalData: [],
      particularData: [],
      commentInput: '',
      store: store,
      // detailsTaskId: null,
    }
  },
  created() {
    this.store.loadData();
  },
  methods: {
    loadData() {
      if (localStorage.getItem(localKey) && JSON.parse(localStorage.getItem(localKey)).commonQuery.length > 0 && JSON.parse(localStorage.getItem(localKey)).myselfQuery.length > 0) {
        let data = JSON.parse(localStorage.getItem(localKey));
        // console.log('get', data)

        // Пока что оба списка получаются из локального хранилища
        // В дальнейшем, эта будет информация получаемая с сервера и локального сохранения не потребуется
        this.commonQuery = data.commonQuery;
        this.myselfQuery = data.myselfQuery;
        // console.log('load...')
      }
      else {
        this.commonQuery = dataList;
        console.log('value is empty')
      }
    },
    saveData() {
      try {
        let data = JSON.stringify({'commonQuery': this.commonQuery, 'myselfQuery': this.myselfQuery});
        // console.log('set', data)
        localStorage.setItem(localKey, data);
        // console.log('success saving')
      }
      catch {
        console.log('saving error')
      }

    },

    takeTask(id) {
      let task = this.commonQuery.filter(item => item.id === id);
      this.commonQuery = this.commonQuery.filter(item => item.id != id);
      this.myselfQuery.push(task[0]);

      this.saveData();
    },

    returnTask(id) {
      let task = this.myselfQuery.filter(item => item.id === id);
      this.myselfQuery = this.myselfQuery.filter(item => item.id != id);
      this.commonQuery.push(task[0]);
      this.commonQuery = this.commonQuery.sort((a,b) => a.id > b.id);

      this.saveData()
    },

    finalTask() {

    },

    openDetailsDialog(id) {
      console.log(`You see now #${id} task`);
      this.taskDetailsDialogVisible = true;
      this.detailsTaskId = id;
    },
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.list-deferred {
  display: none;
}

</style>
