<template>
    <li class="task-item">
        <el-card shadow="hover" class="tasks__item">
            <expires-widget></expires-widget>
            <p>Договор №{{ generalData.contract_number }} <b class="contract-type">{{this.generalData.contract_type}}</b> {{ this.generalData.debt }} ₽</p>
            <el-button-group>
                <el-button v-on:click="openDetailsTask(generalData.id)" type="primary">Обработать</el-button>
            </el-button-group>
        </el-card>
    </li>
</template>

<script>
import ExpiresWidget from './ExpiresWidget.vue';

import store from '../store/custom';

export default {
    props: ['id'],
    components: {
        ExpiresWidget,
    },
    computed: {
        generalData() {
            return this.store.state.generalData.find(item => item.id == this.id);
        }
    },
    data() {
        return {
            store: store,
        }
    },
    methods: {
        openDetailsTask(id) {
            this.store.state.detailsTaskId = id;
            this.store.state.taskDetailsDialogVisible = true;
        },
    },
}
</script>

<style>
    
.tasks__item {
    width: 360px;
    margin-bottom: 10px
}

</style>