'use strict';

import faker from 'faker';

const localKey = "debtors-app";

let store = {
    
    state: {
        commonIds: [],
        myselfIds: [],
        generalData: [],
        particularData: [],
        taskDetailsDialogVisible: false,
        commentInput: '',
        detailsTaskId: 1, // этому свойству нужно будет задавать дефолтное значение при создании приложения
    },

    loadData() {
        if (localStorage.getItem(localKey)) {
            this.loadLocalData();
        }
        else {
            this.loadDefaultData();
        }
    },

    // Загружаем данные из localStorage
    loadLocalData() {
        let data = JSON.parse(localStorage.getItem(localKey));
        this.state.commonIds = data.commonIds;
        this.state.myselfIds = data.myselfIds;
        this.state.generalData = data.generalData;
        this.state.particularData = data.particularData;
    },

    // Сохраняем данные в localStorage
    saveLocalData() {
        let data = JSON.stringify({'commonIds': this.state.commonIds, 'myselfIds': this.state.myselfIds, 'generalData': this.state.generalData, 'particularData': this.state.particularData});
        localStorage.setItem(localKey, data);
    },

    // Загружаем дефолтные данные при отсутствии данных в localStorage
    loadDefaultData() {
        this.state.generalData = generateFakeData(50);
        this.state.commonIds = generateIdList(this.state.generalData);
        this.saveLocalData();
    },

    // Получаем подробные сведения о задаче
    getDetailsTask(id) {

        if (!this.state.particularData.find(item => item.id === id)) {
            this.state.particularData.push(generateFakeItem(id));
        } 

        let result = this.state.particularData.find( item => item.id === id);
        
        return result;
    },

    getLocateName(id) {
        let result= '';
        if (this.state.myselfIds.includes(id)) {
            result = "myself";
        }
        else if (this.state.commonIds.includes(id)) {
            result = "commonQuery";
        }
        // else {
        //     result = "ПИЗДЕЦ((";
        // }
        return result;
    },

    // Оператор откладывает в свой список задачу
    takeTask(id) {
        this.state.commonIds = this.state.commonIds.filter(item => item !== id);
        this.state.myselfIds.push(id);
        this.saveLocalData();
    },

    // Оператор отказался обрабатывать задание
    returnTask(id) {
        this.state.commonIds.push(id);
        this.state.myselfIds = this.state.myselfIds.filter(item => item !== id);
        this.saveLocalData();
    },

    // Завершить обработку задачи
    finishTask(id) {
        if (this.getLocateName(id) === 'myself') {
            this.state.myselfIds = this.state.myselfIds.filter(item => item !== id);
        }

        else if (this.getLocateName(id) === 'commonQuery') {
            this.state.commonIds = this.state.myselfIds.filter(item => item !== id);
        }
        this.state.taskDetailsDialogVisible = false;
    },
}

// Генерация фейковый массив generalData
function generateFakeData(count) {
    let result = [];
    
    for (let i = 1; i <= count; i++) {
        result.push({
            id: i, 
            priority: Math.round(Math.random() * 1000),
            engaged: false,
            contract_number: "123/456" + i,
            contract_type: (Math.random() > .5) ? "PDL" : "Installment",
            debt: Math.round(i * 1000 * Math.random()),
            expires: (Math.random > .5) ? 'urgently' : 'waiting',
            delay: Math.round(Math.random() * 20),
        });
    }

    return result;
}

// Генерация фейкового массива (перечень id) элементов, которые находятся в общей очереди
function generateIdList(list) {
    let sortedList = list.sort((a,b) => a.priority > b.priority);
    
    let result = [];

    for (let item of sortedList) {
        result.push(item.id)
    }

    return result;
}

// Генерация фейковых Подробных данных о задаче
function generateFakeItem(id) {
    return {
        id: id,
        promiser: {
            fullname: faker.name.findName(),
            contacts: [

                {
                    owner: 'self',
                    phone: faker.phone.phoneNumber("(###) ###-####"),
                    
                },

                {
                    owner: 'супруг(а)',
                    phone: faker.phone.phoneNumber("(###) ###-####"),
                },

                {
                    owner: 'работодатель',
                    phone: faker.phone.phoneNumber("(###) ###-####"),
                },

            ]
        },
        comments: [

            {
                author: 'developer',
                content: 'First comment.',
                modified: Date.now()
            },

            {
                author: 'developer',
                content: 'Second comment.',
                modified: Date.now()
            },

            {
                author: 'developer',
                content: 'Third comment. Yeap!',
                modified: Date.now()
            },

        ]
    }
}


export default store;
